import React from "react";
import TableCell from "@mui/material/TableCell";
import IntlMessages from "@crema/utility/IntlMessages";
import TableRow from "@mui/material/TableRow";
import { Fonts } from "../../../shared/constants/AppEnums";

const TableHeading = () => {
  return (
    <TableRow
      sx={{
        "& th": {
          fontSize: 13,
          padding: 2,
          fontWeight: Fonts.BOLD,
          "&:first-of-type": {
            pl: 5,
          },
          "&:last-of-type": {
            pr: 5,
          },
        },
      }}
    >
      <TableCell align="center" className="tableCell">
        <IntlMessages id="battle.table.battleName" />
      </TableCell>
      <TableCell align="center" className="tableCell">
        <IntlMessages id="battle.table.collectionName" />
      </TableCell>
      <TableCell align="center" className="tableCell">
        <IntlMessages id="battle.table.totalParticipant" />
      </TableCell>
   
      <TableCell align="center" className="tableCell">
        <IntlMessages id="battle.table.winner" />
      </TableCell>
      <TableCell align="center" className="tableCell">
        <IntlMessages id="battle.table.time" />
      </TableCell>
    
    </TableRow>
  );
};

export default TableHeading;
