import React from "react";
import TableCell from "@mui/material/TableCell";
import { Box } from "@mui/material";
import Avatar from "@mui/material/Avatar";
import PageviewIcon from "@mui/icons-material/Pageview";
import TableRow from "@mui/material/TableRow";
import { green, red } from "@mui/material/colors";
import { Fonts } from "../../../shared/constants/AppEnums";
import { BattleTableData } from "types/models/battleTable";
// import { ReactComponent as GoldMedal } from "../../../assets/icon/goldRibbon.svg";
// import { ReactComponent as SilverMedal } from "../../../assets/icon/silverRibbon.svg";
// import { ReactComponent as BronzeMedal } from "../../../assets/icon/bronzeRibbon.svg";

interface TableItemProps {
  row: BattleTableData;
}

const TableItem: React.FC<TableItemProps> = ({ row }) => {
  return (
    <TableRow
      sx={{
        "& .tableCell": {
          fontSize: 13,
          padding: 2,
          whiteSpace: "nowrap",
          "&:first-of-type": {
            pl: 5,
          },
          "&:last-of-type": {
            pr: 5,
          },
        },
      }}
      className="item-hover"
    >
      
      <TableCell align="center" className="tableCell">
        {row.battleName}
      </TableCell>
      <TableCell align="center" className="tableCell">
        {row.collectionName}
      </TableCell>
      <TableCell align="center" className="tableCell">
      {row.totalParticipant}
      </TableCell>
      <TableCell align="center" className="tableCell">
      {row.winner}
      </TableCell>
      <TableCell align="center" className="tableCell">
      {row.time}
      </TableCell>
  
    </TableRow>
  );
};

export default TableItem;
